.button {
    background-color: #61dafb;
    padding: 0 40px;
    font-weight: bold;
    font-size: 4rem;
    color: #282c34;
    border-radius: 20px;
}

.button:hover {
    cursor: pointer;
}

.disabledButton {
    opacity: .4;
}

.disabledButton:hover {
    cursor: not-allowed;
}